<template>
  <div class="main">
     <el-container class="home_container">
            <el-header>
                <div class="header">
                     <img src="https://www.nsu.edu.cn/img/logo.png" alt="">
                </div>
            </el-header>
            </el-container>
    <div class="innerBox">
      <div>
        <h1>登 录</h1>
        <el-form :rules="rules" class="login-form" :model="loginForm" @keyup.enter.native="login">
          <el-form-item prop="username">
            <el-input placeholder="请输入学号" prefix-icon="el-icon-s-custom" type="text" v-model="loginForm.student_id"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input placeholder="请输入密码" prefix-icon="el-icon-lock" type="password" v-model="loginForm.password" show-password></el-input>
          </el-form-item>
          <el-form-item>
             <el-button :loading="loading" type="primary" style="width: 100%" @click="login">登 录</el-button>
          </el-form-item>
           <el-form-item class="forget">
          <router-link
            :to="{ path: '/register'}"
            replace
          >还没有注册?点击注册</router-link>
        </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      loginForm:{
        student_id:'',
        password:''
      },
      loading: false,
      rules: {
        student_id: [{ required: true, message: '请输入用户名', trigger: 'biur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods:{
    async login(){
      const{data:res} = await this.$http.post('/api/users/login',this.loginForm)
       if(res.code == 200){
        this.$message.success("登录成功！")
        localStorage.setItem('token',res.data.token)
        this.$router.push('/home')
      }
      if(res.code == 100){
      this.$message.error("登录失败！")
    }
    }

  }
}
</script>

<style  scoped>
