     background-color: rgba(255,255,255,0.7);
  border-radius: 0;
  border: 1px solid #0094ff;
  color: rgba(0, 0, 0, .7)
}
.el-input__inner::placeholder {
  color: rgba(0, 0, 0, .3)
}
