<script>
export default {
  data() {
    return {
      Form: {
        student_name: "",
        student_num: "",
        student_level: "",
        student_year: "",
        student_spec: "",
        student_class: "",
      },
      istest: false,
      themes: false,
      thetest: true,
    };
  },
  created() {
    this.getmessage();
  },
  methods: {
    async getmessage() {
      const token = localStorage.getItem("token");
      // const{data:res} = await this.$http.post('/api/selectstudent?token=' + token);
      const { data: res } = await this.$http.post("/api/selectstudent", {
        token: token,
      });

      this.Form = res.data[0];
    },
    daxuewuli() {
      this.$router.push("/daxuewulishiyan");
    },
    danbai() {
      this.$router.push("/danbai");
    },
    intotest() {
      this.istest = false;
      this.themes = false;
      this.thetest = true;
    },
    selfmes() {
      this.istest = true;
      this.themes = true;
      this.thetest = false;
    },
  },
};
</script>

